import { computed, defineComponent, ref } from 'vue';
import pageSearch from '@/components/page-search';
import pageContent from '@/components/page-content';
import { contentTableConfig } from './config/table.config';
import { SearchFormConfig } from './config/search.config';
import { usePageSearch } from '@/hooks/use-page-search';
import PageModel from '@/components/page-model/src/page-model.vue';
import { ModelFormConfig } from './config/model.config';
import { usePageModel } from '@/hooks/use-page-model';
import { useStore } from 'vuex';
export default defineComponent({
  name: 'role',
  props: {},
  components: {
    PageModel,
    pageContent,
    pageSearch
  },
  setup() {
    const [pageContentRef, headelResetBtnClick, headelQueryBtnClick] = usePageSearch();
    const newBtn = '';
    const [pageModelRef, defaultdata, headerNewData, headerEditData] = usePageModel();
    const store = useStore();
    const menu = computed(() => store.state.entireMenu);
    const overInfo = ref({});
    const handleCheckChange = (data1, data2) => {
      const checkedKeys = data2.checkedKeys;
      const halfCheckedKeys = data2.halfCheckedKeys;
      const menuList = [...checkedKeys, ...halfCheckedKeys];
      overInfo.value = {
        menuList
      };
    };
    return {
      SearchFormConfig,
      contentTableConfig,
      headelResetBtnClick,
      headelQueryBtnClick,
      pageContentRef,
      newBtn,
      ModelFormConfig,
      pageModelRef,
      defaultdata,
      headerNewData,
      headerEditData,
      menu,
      handleCheckChange,
      overInfo
    };
  }
});